<template>
  <div class="row">
    <div class="col-12 mt-3" v-if="isReady">
      <div class="row">
        <div class="col-12 flex-x-between mb-3">
          <div class="flex-align-center">
            <el-button
              v-ripple.400
              type="primary"
              circle
              class="no-width flat"
              @click="$router.go(-1)"
            >
              <i class="bx bx-left-arrow-alt"></i>
            </el-button>
            <h5 class="my-0 ml-2">{{ delivery.name }}</h5>
          </div>
          <div class="flex-align-center flex-end" v-if="startEditing">
            <el-button
              @click="cancelEdit"
              type="primary"
              v-ripple.400
              class="flat"
            >
              Discard
            </el-button>
            <el-button
              :disabled="!startEditing"
              :loading="saving"
              v-ripple.400
              @click="Post"
              type="primary"
            >
              Save
            </el-button>
          </div>
        </div>
        <div class="col-lg-8">
          <div class="box">
            <p class="mt-0 mb-2 text-bold">Details</p>
            <label for="name" class="custom-label">Name</label>
            <el-input name="name" class="w-100 mb-3" v-model="delivery.name" />
            <div class="row">
              <div class="col-6">
                <label for="Address" class="custom-label">Address</label>
                <el-input
                  name="Address"
                  class="w-100 mb-3"
                  v-model="delivery.address"
                />
              </div>
              <div class="col-6">
                <label for="Phone" class="custom-label">Phone number</label>
                <el-input
                  name="Phone"
                  type="number"
                  class="w-100 mb-3"
                  v-model="delivery.telephone"
                />
              </div>
            </div>
            <div class="row">
              <div class="col-6">
                <label for="Remark" class="custom-label">Remark</label>
                <el-input
                  name="Remark"
                  type="url"
                  class="w-100"
                  v-model="delivery.Remark"
                />
              </div>
              <div class="col-6">
                <label for="Priority" class="custom-label">Priority</label>
                <el-input
                  name="Priority"
                  type="url"
                  class="w-100"
                  v-model="delivery.priority"
                />
              </div>
            </div>
          </div>

          <div class="box mt-base">
            <p class="mt-0 mb-2 text-bold">Contact Person</p>
            <div class="row">
              <div class="col-6">
                <label for="name" class="custom-label">Name</label>
                <el-input
                  name="name"
                  class="w-100"
                  v-model="delivery.contact_person"
                />
              </div>
              <div class="col-6">
                <label for="Phone" class="custom-label">Phone number</label>
                <el-input
                  name="Phone"
                  type="number"
                  class="w-100"
                  v-model="delivery.cp_mobile"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      saving: false,
      delivery: {},
      loading: null,
      startEditing: false,
      deliveryBeforeEdit: "",
    };
  },
  watch: {
    delivery: {
      handler: function (val) {
        if (this.deliveryBeforeEdit == JSON.stringify(val)) {
          this.startEditing = false;
        } else {
          this.startEditing = true;
        }
      },
      deep: true,
    },
  },
  created() {
    this.filterdelivery();
  },
  beforeMount() {
    window.addEventListener("beforeunload", this.preventNav);
    this.$once("hook:beforeDestroy", () => {
      window.removeEventListener("beforeunload", this.preventNav);
    });
  },
  beforeRouteLeave(to, from, next) {
    if (this.startEditing) {
      this.$smalltalk
        .confirm(
          "Question",
          "Do you really want to leave? you have unsaved changes!"
        )
        .then(() => {
          next();
        })
        .catch(() => {
          next(false);
        });
    } else {
      next();
    }
  },
  methods: {
    preventNav(event) {
      if (!this.startEditing) return;
      event.preventDefault();
      event.returnValue = "";
    },
    filterdelivery() {
      this.$store.commit("setIsReady", false);
      this.$axios
        .get("delivery/?delivery_id=" + this.$route.params.id)
        .then((response) => {
          this.delivery = response.data;
          this.deliveryBeforeEdit = JSON.stringify(response.data);
          setTimeout(() => {
            this.startEditing = false;
          }, 10);
          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    cancelEdit() {
      this.delivery = JSON.parse(this.deliveryBeforeEdit);
      setTimeout(() => {
        this.startEditing = false;
      }, 10);
    },
    Post() {
      this.saving = true;
      this.$axios
        .put("delivery/", { data: this.delivery })
        .then((res) => {
          this.saving = false;
          this.deliveryBeforeEdit = JSON.stringify(res.data);
          this.delivery = res.data;
          setTimeout(() => {
            this.startEditing = false;
          }, 10);
          this.$noti.display({
            type: "success",
            title: "Success",
            message: "Cool! Delivery has been successfully updated.",
          });
        })
        .catch((err) => {
          this.saving = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
  },
  computed: {
    ...mapState(["isReady"]),
  },
};
</script>

